* {
    margin: 0;
    box-sizing: border-box;
    font-family: "Source Sans Pro", sans-serif;
}

body {
    background: rgb(1, 1, 1);
    background: linear-gradient(121deg, rgba(1, 1, 1, 1) 0%, rgba(6, 6, 8, 1) 100%);
    overflow-x: hidden;
}

body::-webkit-scrollbar {
    width: 0px;
    background-color: rgba(0, 0, 0, 0);
}
